@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
.header-container{
    width: 100%;
    height: 70px;
    background-color: #fff;
    border-bottom:1px solid #eaeaea;
    padding:10px 30px;
}

.header-container .user-container{
    width:auto;
    height: 100%;
    padding:7px;
    background-color: rgba(250, 250, 250, 1);
    border-radius: 7px;
    display:flex;justify-content: center;
    align-items: center;
    cursor: pointer;
}

.header-container .user-container span{
    margin-right: 10px;
}

.header-container .user-container:hover{
    background-color: rgba(234, 234, 234, 1);
}

.avatar-container {
    position: fixed;
    top: 0;
    right: 0;
}
.task-item{
    width:100%;
    height: auto;
    padding:20px 10px;
    margin-top:10px;
    border-radius: 7px;
    border:1px solid #eaeaea;
}
.taskList-container{
    padding: 10px 20px;
}

.add-container{
    position:fixed;
    bottom:30px;
    right:30px;
}
body {
  background-color: rgba(234, 234, 234, 1);
  font-family: 'Open Sans', sans-serif;
}

.login {
  width: 400px;
  margin: 16px auto;
  font-size: 16px;
}

/* Reset top and bottom margins from certain elements */
.login-header,
.login p {
  margin-top: 0;
  margin-bottom: 0;
}

/* The triangle form is achieved by a CSS hack */
.login-triangle {
  width: 0;
  margin-right: auto;
  margin-left: auto;
  border: 12px solid transparent;
  border-bottom-color: #28d;
}

.login-header {
  background: #28d;
  padding: 20px;
  font-size: 1.4em;
  font-weight: normal;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
}

.login-container {
  background: #ebebeb;
  padding: 12px;
}

/* Every row inside .login-container is defined with p tags */
.login p {
  padding: 12px;
}

.login input {
  box-sizing: border-box;
  display: block;
  width: 100%;
  border-width: 1px;
  border-style: solid;
  padding: 16px;
  outline: 0;
  font-family: inherit;
  font-size: 0.95em;
}

.login input[type="email"],
.login input[type="password"] {
  background: #fff;
  border-color: #bbb;
  color: #555;
}

/* Text fields' focus effect */
.login input[type="email"]:focus,
.login input[type="password"]:focus {
  border-color: #888;
}

.login input[type="submit"] {
  background: #28d;
  border-color: transparent;
  color: #fff;
  cursor: pointer;
}

.login input[type="submit"]:hover {
  background: #17c;
}

/* Buttons' focus effect */
.login input[type="submit"]:focus {
  border-color: #05a;
}
